html,
body {
    margin: 0;
    padding: 0;
    overflow: hidden;
}

pre,
code {
    background: #fff;
}

table,
th,
td {
    border: 1px solid black !important;
    padding: 10px 15px;
}

head {
    background: red;
}

/** Top-pane */
.navbar-custom {
    width: 100vw;
    height: 3rem;
    background: #ddd;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
}

.dropdown-menu {
    width: 20rem;
    margin: .9rem 0 0 -19.7rem;
    border-radius: 0;
}

.dropdown-menu::before {
    content: "";
    position: absolute;
    bottom: 100%;
    right: 0;
    width: 0;
    height: 0;
    border-left: 26px solid transparent;
    border-right: 26px solid transparent;
    border-bottom: 13px solid white;
}

nav ul {
    list-style-type: none;
}

nav li {
    float: left;
}

#dropdownId {
    color: inherit;
}